import Keycloak from 'keycloak-js';
import axios from 'axios';

// Debugging helper
function debugLog(...args) {
  console.log('[Keycloak Debug]', ...args);
}

// Keycloak configuration
const keycloakConfig = {
  url: 'http://localhost:8080',
  realm: 'areti-alliance',
  clientId: 'areti-crm-client'
};

// Create a module-level mock user in case Keycloak fails
const mockUser = {
  name: 'Development User',
  email: 'dev@example.com',
  role: 'admin',
  token: 'mock-token-for-development',
  timestamp: new Date().getTime()
};

// Authentication service with simplified approach
const AuthService = {
  // Initialize - always succeeds by design
  init: () => {
    debugLog('Simplified init called');
    
    // Check if we already have a user in localStorage
    const existingUser = localStorage.getItem('aretiUser') || localStorage.getItem('aretiUser_backup');
    if (existingUser) {
      debugLog('Found existing user in localStorage');
      return Promise.resolve(true);
    }
    
    // Store mock user for development
    debugLog('Storing mock user for development');
    localStorage.setItem('aretiUser_backup', JSON.stringify(mockUser));
    
    // Always return success
    return Promise.resolve(true);
  },

  // Login - simplified for development
  login: () => {
    debugLog('Simplified login called');
    
    // Store mock user
    localStorage.setItem('aretiUser_backup', JSON.stringify(mockUser));
    
    // Reload the page to simulate login completion
    window.location.reload();
  },

  // Logout - simplified for development
  logout: () => {
    debugLog('Simplified logout called');
    
    // Clear storage
    localStorage.removeItem('aretiUser');
    localStorage.removeItem('aretiUser_backup');
    
    // Reload the page
    window.location.reload();
  },

  // Always returns mock user
  getUserProfile: () => {
    debugLog('Getting user profile');
    
    // Try from localStorage first
    const user = localStorage.getItem('aretiUser') || localStorage.getItem('aretiUser_backup');
    if (user) {
      try {
        return JSON.parse(user);
      } catch (error) {
        debugLog('Error parsing user data, returning mock user');
      }
    }
    
    // Return mock user as fallback
    return mockUser;
  },

  // Always return authenticated for development
  isAuthenticated: () => {
    return true;
  },

  // Return mock auth header
  getAuthHeader: () => {
    return {
      'Authorization': `Bearer ${mockUser.token}`
    };
  }
};

export default AuthService;